import React from "react"
import FluidImage from "../../components/FluidImage"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
// import { blogURI } from "../../../globals"

const POST_QUERY = graphql`

    query New {
        wpgraphql {
            posts(first: 6, where: {categoryId: 34}) {
                nodes {
                    title
                    excerpt
                    uri
                    featuredImage {
                        altText
                        sourceUrl
                        imageFile {
                            childImageSharp {
                                fluid(maxHeight: 600, maxWidth: 1000, quality: 100, cropFocus: CENTER) {
                                    ...GatsbyImageSharpFluid_withWebp_tracedSVG
                                }
                            }
                        }
                    }
                    CustomPost {
                        acreage
                        address
                        bedroom
                        fieldGroupName
                        price
                        wc
                    }
                }
            }
        }
    }
`
const FeaturedNew = ({ title }) => {
    return (
        <section className="featured-category pt-4">
            <div className="container">
                <div className="title text-center w-100">
                    <h2>{title}</h2>
                </div>
                <div className="list-post w-100">
                    <div className="row">
                        <StaticQuery
                            query={POST_QUERY}
                            render={(data) => {

                                if (data.wpgraphql.posts) {
                                    const posts = data.wpgraphql.posts.nodes

                                    return (
                                        <>
                                            {
                                                posts &&
                                                posts.map((prop, index) => (

                                                    <div className="col-md-4 col-xs-12" key={index}>
                                                        <article className="item">
                                                            <div className="featured-image">
                                                                <Link to={`${prop.uri}`}>
                                                                    <div className="ov1"></div>
                                                                    <div className="ov2"></div>
                                                                    <div className="ov3"></div>
                                                                    <div className="ov4"></div>
                                                                    <FluidImage image={prop.featuredImage} alt={prop.title} />
                                                                </Link>
                                                            </div>
                                                            <div className="title">
                                                                <Link to={`${prop.uri}`} title={prop.title}>
                                                                    <h3>{prop.title}</h3>
                                                                </Link>
                                                            </div>
                                                            <div className="excerpt">
                                                                <div dangerouslySetInnerHTML={{ __html: prop.excerpt.replace(/<p class="link-more.*/, '') }} />
                                                            </div>
                                                        </article>
                                                    </div>
                                                ))
                                            }
                                        </>
                                    )
                                }
                                return null
                            }}
                        />
                    </div>
                    <div className="link-category text-center pb-5">
                        <Link to={`tin-tuc`}>
                            Xem thêm
                        </Link>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default FeaturedNew


