import React from "react"
import Slider from "../layouts/Slider"
import About from "../layouts/About"
import WhyChoose from "../layouts/WhyChoose"
import FeaturedCategory from "../layouts/FeaturedCategory"
import Investor from "../layouts/Investor"
import FeaturedSellHouse from "../layouts/FeaturedSellHouse"
import FeaturedBuyHouse from "../layouts/FeaturedBuyHouse"
import FeaturedNew from "../layouts/FeaturedNew"
import Content from "../layouts/Content"
import RightImage from "../layouts/RightImage"
import LeftImage from "../layouts/LeftImage"
const AllLayouts = ({ componentData, componentType }) => {

  const Default = () => <div>In AllLayouts the mapping of this component is missing: {componentType}</div>

  const components = {
    page_Pagebuilder_Layouts_Slider: Slider,
    page_Pagebuilder_Layouts_About: About,
    page_Pagebuilder_Layouts_WhyChoose: WhyChoose,
    page_Pagebuilder_Layouts_FeaturedCategory: FeaturedCategory,
    page_Pagebuilder_Layouts_Investor: Investor,
    page_Pagebuilder_Layouts_FeaturedSellHouse: FeaturedSellHouse,
    page_Pagebuilder_Layouts_FeaturedBuyHouse: FeaturedBuyHouse,
    page_Pagebuilder_Layouts_FeaturedNew: FeaturedNew,
    page_Pagebuilder_Layouts_Content: Content,
    page_Pagebuilder_Layouts_RightImage:RightImage,
    page_Pagebuilder_Layouts_LeftImage:LeftImage,
    page_default: Default
  }

  const mappedData = (componentType, componentData) => {
    switch (componentType) {
      default:
        return componentData
    }
  }

  const ComponentName = components[componentType] ? components[componentType] : components['page_default']

  return (
    <ComponentName {...mappedData(componentType, componentData)} />
  )
}

export default AllLayouts
