import React from "react"
import FluidImage from "../../components/FluidImage"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
// import { blogURI } from "../../../globals"
import bed from "../../images/bed.svg"
import bath from "../../images/bath.svg"
import area from "../../images/area.svg"
const POST_QUERY = graphql`

    query SellHouse {
        wpgraphql {
            posts(first: 6, where: {categoryId: 40}) {
                nodes {
                    title
                    excerpt
                    uri
                    featuredImage {
                        altText
                        sourceUrl
                        imageFile {
                            childImageSharp {
                                fluid(maxHeight: 600, maxWidth: 1000, quality: 100, cropFocus: CENTER) {
                                    ...GatsbyImageSharpFluid_withWebp_tracedSVG
                                }
                            }
                        }
                    }
                    CustomPost {
                        acreage
                        address
                        bedroom
                        fieldGroupName
                        price
                        wc
                    }
                }
            }
        }
    }
`
const FeaturedSellHouse = ({ title }) => {
    return (
        <section className="featured-category pt-4">
            <div className="container">
                <div className="title text-center w-100">
                    <h2>{title}</h2>
                </div>
                <div className="list-post w-100">
                    <div className="row">
                        <StaticQuery
                            query={POST_QUERY}
                            render={(data) => {

                                if (data.wpgraphql.posts) {
                                    const posts = data.wpgraphql.posts.nodes

                                    return (
                                        <>
                                            {
                                                posts &&
                                                posts.map((prop, index) => (

                                                    <div className="col-md-4 col-xs-12" key={index}>
                                                        <article className="item">
                                                            <div className="featured-image">
                                                                <Link to={`${prop.uri}`}>
                                                                    <div className="ov1"></div>
                                                                    <div className="ov2"></div>
                                                                    <div className="ov3"></div>
                                                                    <div className="ov4"></div>
                                                                    <FluidImage image={prop.featuredImage} alt={prop.title} />
                                                                </Link>
                                                            </div>
                                                            <div className="title">
                                                                <Link to={`${prop.uri}`} title={prop.title}>
                                                                    <h3>{prop.title}</h3>
                                                                </Link>
                                                            </div>
                                                            <div className="excerpt">
                                                                <div className="address">
                                                                    <span><i className="fa fa-map-marker" aria-hidden="true"></i>{prop.CustomPost.address}</span>
                                                                </div>
                                                                <div className="price">
                                                                    <span>Giá: ~ {prop.CustomPost.price}</span>
                                                                </div>
                                                                <div className="d-flex justify-content-between">
                                                                    <div>
                                                                        <span><img src={bed} alt="bed" />{prop.CustomPost.bedroom}</span>
                                                                    </div>
                                                                    <div>
                                                                        <span><img src={bath} alt="bath" />{prop.CustomPost.wc}</span>
                                                                    </div>
                                                                    <div>
                                                                        <span><img src={area} alt="area" />{prop.CustomPost.acreage}</span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </article>
                                                    </div>
                                                ))
                                            }
                                        </>
                                    )
                                }
                                return null
                            }}
                        />
                    </div>
                    {/* <div className="link-category text-center pb-5">
                        <Link to={`mua-nha`}>
                            Xem thêm
                        </Link>
                    </div> */}
                </div>
            </div>
        </section>
    )
}

export default FeaturedSellHouse


