import React from "react"

import Layout from "../../components/Layout"
import SEO from "../../components/SEO"
import AllLayouts from "../AllLayouts"


const Page = ({ pageContext }) => {
  const {
    page: { title, pageBuilder, pageId, featuredImage },
  } = pageContext

  const layouts = pageBuilder.layouts || []

  return (
    <Layout>
      <SEO
        title={title}
        description={title + " Sàn bất động sản uy tín, chuyên nghiệp Alphaland"}
        keywords={title + " Sàn bất động sản, nhà đất tp hcm, dự án bất động sản, chung cư tphcm, nhà đất Bình Dương, Nhà đất Đồng Nai"}
        seoImage={'https://www.sharkhung.vn' + (featuredImage? featuredImage.imageFile.childImageSharp.fluid.src:'/ogimage-default.jpg')}
      />
      {(() => {
        if (pageId === 15) {
          return (
            <></>
          )
        } else {
          return (
            <div className="big-title mb-5">
              <div className="container text-center">
                <h1> {title} </h1>
              </div>
            </div>

          )
        }
      })()}

      {
        layouts.map((layout, index) => {
          return <AllLayouts key={index} componentType={layout.fieldGroupName} componentData={layout} />
        })
      }

    </Layout>
  )
}

export default Page
